import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProductRoutingModule } from './product-routing.module';
import { ProductComponent } from './product.component';
import { ProductlistViewComponent } from './productlist-view/productlist-view.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ServicelistViewComponent } from './servicelist-view/servicelist-view.component';
import { OfferlistViewComponent } from './offerlist-view/offerlist-view.component';
import { MaterialExampleModule } from 'src/app/material.module';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { CloneServiceComponent } from './clone-service/clone-service.component';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';




@NgModule({
  declarations: [
    ProductComponent,
    ProductlistViewComponent,
    ServicelistViewComponent,
    OfferlistViewComponent,
    CloneServiceComponent
  ],
  imports: [
    CommonModule,
    ProductRoutingModule,
    BsDatepickerModule.forRoot(),
    FormsModule,
    ReactiveFormsModule,
    MaterialExampleModule,
    MatDatepickerModule
  ]
})
export class ProductModule { }
